.container__header {
    align-items: center;
    display: flex;
    flex-direction: column;
}

.container__header h1 {
    font-family: LibreFranklin;
    font-weight: 100;
    font-size: 7rem;
    letter-spacing: -.6rem;
    margin-right: 3rem;
}

.container__header h2 {
    color: #090f08;
    font-family: LibreFranklin;
    font-weight: 400;
    font-size: 9rem;
    letter-spacing: -.6rem;
    transform: rotateX(180deg);
    transform-style: preserve-3d;
    transition: transform 1.5s;
}

.container__header h2:hover {
    transform: rotateX(0deg);
    transition: 1.5s;
}

.container__addIdea {
    border-left: 1px solid #090f08;
    border-right: 1px solid #090f08;
    display: flex;
    flex-direction: column;
    margin: 1rem auto;
    padding: 1rem 4rem;
    align-content: center;
    max-width: 40vw;
}

.container__addIdea__input {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 70%;
}

.container__addIdea__input label {
    color: #515f4c;
    font-size: 3rem;
    font-weight: 100;
    letter-spacing: -.2rem;
    text-align: center;
}

.container__addIdea__input input {
    background-color: #c2d1b9;
    border: none;
    border-bottom: 1px solid #515f4c;
    font-family: LibreFranklin;
    font-size: 1.2rem;
    margin: 1rem auto;
    outline: none;
    padding-bottom: .3rem;
    text-align: center;
    width: 100%;
}

.container__addIdea button {
    background-color: #090f08;
    border: none;
    border-radius: 5px;
    box-shadow: 0 1px 3px #afafaf;
    color: #c8d2d6;
    cursor: pointer;
    font-family: LibreFranklin;
    font-weight: 100;
    margin: 0 auto .5rem;
    padding: .4rem 0;
    width: 80px;
}

.container__addIdea button:hover {
    background-color: #131f11;
}

.userIdeas {
    margin: 0 auto;
    width: 30vw;
}

.userIdeas h3 {
    font-size: 3rem;
    font-weight: 100;
    letter-spacing: -.1rem;
    text-align: center;
}

.userIdeas__list {
    border-left: 1px solid #1e2e21;
}

.userIdeas__list button {
    background-color: #c2d1b9;
    border: 1px solid #7d867b;
    border-radius: 100px;
    color: #1e2e21;
    font-family: LibreFranklin;
    font-weight: 900;
    margin-left: .5rem;
    padding: .2rem .4rem;
}